@import './css/ad_layout.css';

#char2:before { content: "M" }
#char1:before { content: "A" }
body.isPm #char1:before { content: "P" }

body.noPeriod #char1,
body.noPeriod #char2,
body.noSeconds #digit1,
body.noSeconds #digit2{
  visibility: hidden;
}
body.noPeriod.noSeconds #char1,
body.noPeriod.noSeconds #char2,
body.noPeriod.noSeconds #digit1,
body.noPeriod.noSeconds #digit2{
  display: none;
}

#timeTable { margin: auto }
#timeTable td { min-width: 100% }
#timeRow > table {width: 100%} /* for metro app */

#digit1,
#digit2,
#char1,
#char2{
 font-size: 46%;
 /*
 line-height: .7em;
 */
 line-height: .9em;
 vertical-align: top;
}
#digit1,
#digit2{
  padding-bottom: .2em;
}
#char1,
#char2{
  /*side padding to make sure that width of char's cell > width of digit's cell*/
  padding-left: .05em;
  padding-right: .05em;
}
body:not(.noPeriod)  #time,
body:not(.noSeconds) #time{
  padding-right: .2em;
}


/* checkbox styling
{{{*/
input[type='checkbox'] {
    background: #292A2D;
    border: solid 1px #5E6064;
    height: 16px;
    width: 28px;
    position: relative;
    /*vertical-align: top;*/
    margin-right: 3px;
    -webkit-appearance: none;
    -webkit-border-radius: 3px;
    -webkit-transition: all 0.5s;
}

input[type='checkbox']:after {
    content: ' ';
    display: block;
    width: 12px;
    height: 12px;
    background: #91949D;
    position: absolute;
    top: 1px;
    left: 13px;
    -webkit-border-radius: 2px;
    -webkit-transition: all 0.5s;
}

input[type='checkbox']:checked {
    border: solid 1px #5BA4D5;
    box-shadow: 0 0 5px #5BA4D5;
}

input[type='checkbox']:checked:after {
    background: #5BA4D5;
    left: 1px;    
}

/*}}}*/

label
{
  margin: 7px;
  /*white-space: nowrap;*/
  display: inline-block;
}
label > *
{
  margin: 2px;
}

label.pointerCursor,
label.pointerCursor > *
{
  cursor: pointer !important;
}

/* vertical align options*/
label:before
{
  content: " ";
  height: 100%;
  width: 0;
}
label:before,
label > *
{
  display: inline-block;
  vertical-align: middle;
}


/* color input playground: http://jsfiddle.net/BC7JD/2/ */
input[type='color']
{
  padding: 0px;
  height: 16px;
  outline: 0;
  border: 1px solid #888;
  border-radius: 3px;
}


*:not(input):not(a)
{
	-moz-user-select: -moz-none;
	-webkit-user-select: none;
	user-select: none;
	cursor: default;
}

#date
{
  opacity: 0.7;
}
#timeTable,#timeTable * { cursor: pointer; }

